import axios from "axios";

import { MANUFAC_FAIL, MANUFAC_REQUEST, MANUFAC_SUCCESS } from "../constants/manufaConstants";


export const manufacturers = ({token,limit,skip}) => async (dispatch) => {
    try {

        dispatch({ type: MANUFAC_REQUEST });
        const config = { headers: { "Authorization": `${token}`} };
        
        const { data } = await axios.get(`https://devapi.dhakai.com/api/v2/manufacturers?limit=${limit}&&skip=${skip}`, config)
        dispatch({ type: MANUFAC_SUCCESS, payload: data.result });
    } catch (error) {
        dispatch({ type: MANUFAC_FAIL, payload: "Server error" })
    }
};