import React from 'react'
import { NavLink } from 'react-router-dom'


import './productgroup.css'

const ProductGroup = () => {

    const productGroups =[
        'All for you',
        'Recommended',
        'Top Manufacturers',
        'Winter Special',
       ' Demin Special',
        'Women’s wear',
        'Kid’s wear',
        'T-shirts',
        'Men’s Fashion',
    ]
  return (
    <div className='pg'>
      <ul>
        {
            productGroups.map((pg, index)=>( 
            <NavLink to={`#${pg}`} key={pg+index} >
               <li>{pg}</li>
            </NavLink>))
           
        }
      </ul>
        
        
    </div>
  )
}

export default ProductGroup