import React from 'react'
import loadsign from "../../images/Group1156.svg"
import './loading.css'
const Loading = () => {
  return (
    <div className='loading'>

        <div>
          <img src={loadsign} alt="loadsign" />
        </div>
        <div>
          Loading...
        </div>
        

       </div>
  )
}

export default Loading