import React from 'react'
import DashCard from './DashCard.js'
import './dashboard.css'
import { useEffect } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import { manufacturers } from '../../actions/manufacActions.js'
import Loading from '../features/Loading.js'
import { useAlert } from 'react-alert'
import { clearErrors } from '../../actions/userActions.js'
import { useState } from 'react'
import Cookies from 'js-cookie'
import { useNavigate } from 'react-router'
import ProductGroup from '../features/ProductGroup.js'
const Dashboard = () => {
  const alert = useAlert()
  const navigate = useNavigate()
  const dispatch = useDispatch();
  const { user } = useSelector((state) => state.user);
  const { loading, data, error } = useSelector(state => state.manufac)
  const [skip, setskip] = useState(0)
  const [limit, setlimit] = useState(8)

  const count = data.count;
  window.onscroll = () => {
    if ((window.innerHeight + document.documentElement.scrollTop +1).toFixed(0)
     === (document.documentElement.offsetHeight).toFixed(0) && limit <= count) {
      setlimit(limit + 4)
      setskip(0)
    }
  }

  useEffect(() => {
    const token = Cookies.get("acc_token");
    if (!token) {
      alert.error('unauthorized')
      dispatch(clearErrors())
      navigate('/login')
    }
    if (error) {
      alert.error(error)
      dispatch(clearErrors())
    }
    dispatch(manufacturers({token, limit, skip }));

  }, [dispatch, user, error, alert, limit, skip,navigate])

  return (
    <>
    <ProductGroup/>
      {
        loading ? <Loading /> :
          <div className='dashboard'>
            {
              data.manufacturers && data.manufacturers.map((data) => (
                <DashCard key={data._id} infos={data} />
              ))
            }

          </div>
      }

    </>
  )
}

export default Dashboard