import React, { useRef } from 'react'
import './Navbar.css'
import note from '../../images/Group5882 (1).svg'
import message from '../../images/Group5882 (2).svg'
import search from '../../images/Group5882 (3).svg'
import profile from '../../images/Group5882 (4).svg'
import { NavLink } from 'react-router-dom'

const TopNav = () => {
    const switchs = useRef(null);
    const switchTab = (tab) => {
        if (tab === "explore") {
            switchs.current.classList.remove("right");
            switchs.current.classList.add('left')
        }
        if (tab === 'activity') {
            switchs.current.classList.remove('left');
            switchs.current.classList.add('right');

        }
    }

    return (
        <>
            <div className="topnav">
                <div className='toggle_btn'>
                    <span onClick={() => switchTab("explore")}>Explore</span>
                    <span onClick={() => switchTab("activity")}>Activity</span>
                    <button ref={switchs}></button>
                </div>
                <div className='searchBox'>
                    <input type="text" id='search' placeholder=' Search by name, group, type and others' />
                    <img src={search} alt={search} />

                </div>
                <div className='notification'>
                    <div className='mess'>
                        <img src={message} alt={message} />
                        <div>0</div>
                    </div>
                    <div className='mess'><img src={note} alt={note} />
                     <div>0</div></div>
                    <div className='profile'><NavLink to={'/login'}><img src={profile} alt={profile} /></NavLink></div>

                </div>
            </div>
        </>
    )
}

export default TopNav