import React from 'react'
import './Navbar.css'
import logo1 from "../../images/Group585 (1).svg"
import logo2 from "../../images/Group585 (2).svg"
import logo3 from "../../images/Group585 (3).svg"
import logo4 from "../../images/Group585 (4).svg"
import logo5 from "../../images/Group585 (5).svg"
import logo6 from "../../images/Group585 (6).svg"
import logo7 from "../../images/Group585 (7).svg"
import logo from "../../images/Group1156.svg"
import setting from "../../images/Iconly_Two-tone_Setting.svg"
import logout from "../../images/Iconly_Two-tone_Logout.svg"
import help from "../../images/help.svg"
 import { NavLink } from 'react-router-dom'
 
 const Navbar = () => {
  return (
    <>
    <div className="navbar">
      <div className='logo'>
        <img src={logo} alt={logo}/>
        </div>
      <div className="navLinkDiv">
   <div className="navlink">
     <NavLink   to='/home'><img src={logo7} alt={logo}/>   </NavLink>
    <img src={logo1} alt={logo}/>
    <img src={logo6} alt={logo}/> 
    <img src={logo5} alt={logo}/>
    <img src={logo4} alt={logo}/>
    <img src={logo3} alt={logo}/>
    <img src={logo2} alt={logo}/>
    
    </div>
    <div className="navlink">
    <img src={setting} alt={setting}/>
    <img src={logout} alt={logout}/>
    <img src={help} alt={help}/>
    


    </div>
        </div>  
 
    
    </div>
    </>
  )
}
export default Navbar