import axios from "axios";
import Cookies from 'js-cookie'
import { CLEAR_ERRORS,  LOGIN_FAIL, LOGIN_REQUEST, LOGIN_SUCCESS } from "../constants/userConstants";


export const login = (email, password, deviceuid) => async (dispatch) => {
    try {
        dispatch({ type: LOGIN_REQUEST });
        const config = { headers: { "Content_Type": "apllication/json" } };
const userData = {
    auth: {
        "email": `${email}`,
        "deviceUuid": `${deviceuid}`
    },
    "password": `${password}`
}
        const { data } = await axios.post(`https://devapi.dhakai.com/api/v2/login-buyer`, userData, config); 

        if (data.statusCode === 400) {
        dispatch({ type: LOGIN_FAIL, payload: data.message })
        
        }else{
          dispatch({ type: LOGIN_SUCCESS, payload: data });
        }
        
       
        if (data.message === 'SUCCESS') {
            let expire =  data.result.expiresAt
          
            Cookies.set("acc_token",`${data.result.token}`, {expires:5});
            Cookies.set("refrashToken", `${data.result.refreshToken}`, {expires:5 })
        }

    } catch (error) {
        dispatch({ type: LOGIN_FAIL, payload: error.response.data.error })
    }
};

export const clearErrors = () => async (dispatch) => {
    dispatch({ type: CLEAR_ERRORS });
};