import { CLEAR_ERRORS, LOGIN_FAIL, LOGIN_REQUEST, LOGIN_SUCCESS } from "../constants/userConstants";

export const userReduser = (state = { user: {} }, action) => {
    switch (action.type) {
        case LOGIN_REQUEST:
            return {
                loading: true,
                isAuthUser: false
            };

        case LOGIN_SUCCESS:
            return {
                ...state,
                loading: false,
                isAuthUser: true,
                user: action.payload
            };


        case LOGIN_FAIL:
            return {
                ...state,
                loading: false,
                isAuthUser: false,
                user: null,
                error: action.payload
            };
        case CLEAR_ERRORS:
            return {
                ...state,
                error: null
            }

        default:
            return state;

    }
};
