import React from 'react'

const DashCard = ({infos}) => {
    const{ minOrderQty,productGroups}=infos;
    const{banners, logo,companyName} = infos.meta;

  return (
    <div className='dashcard' key={infos._id}>
        <div className="banner">
            {
               banners.map((banner)=>(
                  <div key={banner._id}><img src={banner.url} alt={banner.name} /> </div> 
               )) 
            }
        </div>
         
        <div className="infos">
        <div className="logo">
                <img src={logo.url} alt="logo" />
            </div>
            <div className="details">
                <h2>{companyName}</h2>
                <div className="detail">
                    <div className='address'><p>
                        {
                            infos.addresses.map((addr)=>(
                                <span key={addr._id}>{addr.city},{addr.country}</span>
                                
                            ))
                            
                            }
                         </p><div className='dot'></div> <span>Min Qty: {minOrderQty}</span></div>
                   
                    <div><p>{productGroups.map((pg)=>( <span key={pg._id}>{pg.name}</span> ))}..</p></div>
                </div>
                <button>View Details</button>
            </div>
        </div>
    </div>
  )
}

export default DashCard