import React, { useEffect, useState } from 'react'

import './login.css'
import { useDispatch, useSelector } from 'react-redux';

import { login } from '../../actions/userActions'
import { useAlert } from 'react-alert';
import { useNavigate } from 'react-router';
import axios from 'axios';



const Login = () => {
    const navigate = useNavigate()
    const dispatch = useDispatch()
    const alert = useAlert();
    const [deviceuid, setdeviceuid] = useState('')
    const [email, setemail] = useState('');
    const [password, setpassword] = useState('')
    const { error,  isAuthUser } = useSelector(state => state.user)
    const device = async () => {
        const { data } = await axios.get(`https://devapi.dhakai.com/api/v2/deviceuid`)
        setdeviceuid(data.result.deviceUuid)

    }


    const hnadleSubmit = async (e) => {
        e.preventDefault();
        dispatch(login(email, password, deviceuid));
        
    }
  


    useEffect(() => {
        device() 
         if (isAuthUser) {
       navigate("/")
    }
        if (error) {
            alert.error(error)
        }
    }, [error, alert,isAuthUser, navigate])


    return (
        <>
            <div className="login">

                <form onSubmit={hnadleSubmit}>
                    <h2>Login</h2>
                    <input type="email" value={email} onChange={(e) => setemail(e.target.value)} placeholder='Email' required />
                    <input type="password" autoComplete='off' value={password} onChange={(e) => setpassword(e.target.value)} placeholder='password' required />
                    <input type="submit" value={"Submit"} />
                </form>

            </div>
        </>
    )
}

export default Login