import { MANUFAC_FAIL, MANUFAC_REQUEST, MANUFAC_SUCCESS } from "../constants/manufaConstants";
import { CLEAR_ERRORS } from "../constants/userConstants";

export const manufacReduser = (state = { data:{} }, action) => {
    switch (action.type) {
        case MANUFAC_REQUEST:
            return{
                loading: true,
                isAuthUser: false,
                data:[]
            };
            case MANUFAC_SUCCESS:
                return{
                    ...state,
                    loading: false,
                    isAuthUser: true,
                    data: action.payload
                };
              
                case MANUFAC_FAIL:
                    return{
                        ...state,
                        loading:false,
                        isAuthUser: false,
                        user:null,
                        data: action.payload
                    };
                    case CLEAR_ERRORS: 
                    return{
                        ...state,
                        error:null
                    }
                   
        default:
            return state;
            
    }
    };